.About{
    background: #2B2B2B;
    padding-top: rem(60px);
    padding-bottom: rem(60px);
    overflow: hidden;
    &__image{
        position: relative;
        width: 120px;
        height: 120px;
        border-radius: 100%;
        overflow: hidden;
        @media screen and (max-width: 767px) {
            margin: 0 auto;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        } 
    }
    &__desc{
        &--title{
            h2{
                font-size: rem(22px);
                color: #fff;
                margin-bottom: rem(12px);
            }
        }
        p{
            color: #7A7A7A;
            margin-bottom: rem(30px);
        }
        ul{
            list-style: none;
            li{
                margin-bottom: rem(10px);
            }
        }
        .d-flex {
            @media screen and (max-width: 767px) {
                flex-direction: column-reverse;
                display: inline-flex  !important;    
            }
        }
        @media screen and (max-width: 767px) {
            text-align: center;
            margin-top: rem(25px);
        }  
    }
}

.Resume {
    background: #fff;
    padding-top: rem(60px);
    padding-bottom: rem(60px);
    overflow: hidden;
    .ourDetails {
        padding-bottom: rem(25px);
        border-bottom: 1px solid #E8E8E8;
        &:not(:last-child) {
            margin-bottom: rem(50px);  
            @media screen and (max-width: 575px) {
                margin-bottom: rem(30px);
            }   
        }
        @media screen and (max-width: 767px) {
            text-align: center;
        } 
        &__title {
            font-size: rem(18px);
            color: #313131;
            letter-spacing: 1px;
            text-transform: uppercase;
            font-weight: 700;
            span {
                border-bottom: 3px solid #11ABB0;
                padding-bottom: rem(6px);
            }
            @media screen and (max-width: 767px) {
                margin-bottom: rem(25px);
            }     
        }
        h2{
            font-size: rem(25px);
            color: #313131;
            margin-bottom: rem(15px);
        }
        ul{
            display: inline-block;
            margin-left: rem(30px);
            margin-bottom: rem(20px);
            margin-top: rem(20px);
            li{
                margin-bottom: rem(20px);
            }
        }
        p{
            margin-bottom: rem(30px);
        }
        .mySkills {
            &__progress {
                margin-bottom: rem(20px);
                h6 {
                    margin-bottom: rem(20px);
                    text-transform: uppercase;
                    @media screen and (max-width: 767px) {
                        text-align: left;
                    } 
                }
            }
        }
    }  
}
.Work {
    background: #2B2B2B;
    padding: rem(50px) 0;
    h4 {
        color: #fff;
        margin-bottom: rem(25px);
    }
    .card {
        margin-bottom: rem(30px);  
        &__image {  
            height: 200px; 
            overflow: hidden;
            @media screen and (max-width: 991px) {
                height: 150px;
            }
            img {
                transition: .3s ease all;
                width: 100%;
                height: 100%;
                object-fit: cover;
                &:hover {
                    transform: scale(1.2);
                }
            }
        }
        &__desc {
            padding: rem(10px) rem(20px);
            display: flex;
            justify-content: space-between;
            align-items: center;
            &--title {
                color: #fff;
            }
            &--actions {
                .btn {
                    min-width: auto;
                    height: 40px;
                    width: 40px;
                    padding-right: rem(15px);
                    padding-left: rem(15px);
                    @media screen and (max-width: 767px) {
                        margin-bottom: 0;
                    }
                    svg {
                        margin-right: 0;
                    }
                    &:not(:last-child) {
                        margin-right: rem(6px);
                    }
                }
            }
        } 

    }
}

.Contact {
    background: #191919;
    padding: rem(70px) 0;
    @media screen and (max-width: 575px) {
        padding: rem(50px) 0;    
    }
    .title {
        display: flex;
        align-items: center;
        margin-bottom: rem(50px);
        .icon {
            margin-right: rem(35px);
            flex: 0 0 80px;
            max-width: 80px;
            svg {
                font-size: rem(50px);
                color: #ebeeee;
            }
        }
    }
    .control-label {
        flex: 0 0 80px;
        max-width: 80px;
    }
    .button-row {
        margin-top: rem(30px);
        @media screen and (max-width: 767px) {
            justify-content: center;
        } 
    }
    .addressInfo {
        h6 {
            color: #fff;
            margin-bottom: rem(20px);
        }
        ul{
            li{
                margin-bottom: rem(20px);
            }
        }
        @media screen and (max-width: 767px) {
            text-align: center;
        } 
    }
}
